.lso-node-selection-table__table--scroll {
  margin-bottom: var(--pf-v5-global--spacer--md);
  max-height: 300px;
  overflow-y: auto;
  scrollbar-color: var(--pf-v5-global--BackgroundColor--light-300) var(--pf-v5-global--palette--white);
}

.lso-node-selection-table__table--scroll .pf-v5-c-table {
  border: var(--pf-v5-global--BorderWidth--sm) solid var(--pf-v5-global--BorderColor--100);
}

.lso-node-selection-table__table--scroll .pf-v5-c-table .pf-v5-c-table__check {
  width: 4.6% !important;
}

@media screen and (max-width: 1620px) {
  .lso-node-selection-table__table--scroll .pf-v5-c-table .pf-v5-c-table__check {
    width: 7% !important;
  }
}

@media screen and (max-width: 1200px) {
  .lso-node-selection-table__table--scroll .pf-v5-c-table .pf-v5-c-table__check {
    width: 10% !important;
  }
}

